import {
    mapState,
    mapMutations
} from 'vuex';
export default {
  props: {
    mode: {
      type: Number,
      default: 1
    },
    button: {
      type: String,
      default: "outside"
    },
    show: {
      type: Boolean,
      default: true
    },
    radius: {
      type: String,
      default: "60"
    },
    placeholder: {
      type: String,
      default: "请输入搜索内容"
    },
    backgroundColor: {
      type: String,
      default: "#fff"
    },
    border: { type: String, default: "1px #f5f5f5 solid" }
  },
  data() {
    return {
      active: false,
      inputVal: "",
      searchName: "取消",
      isDelShow: false,
      isFocus: true
    };
  },
  computed: {
    ...mapState(['searchHistory'])
  },
  methods: {
    ...mapMutations(['setHistory']),
    focus() {
      this.active = true;
    },
    blur() {
      this.isFocus = false;
      if (!this.inputVal) {
        this.active = false;
      }
    },
    clear() {
      this.inputVal = "";
      this.active = false;
      this.$emit("search", "");
    },
    getFocus() {
      this.isFocus = true;
    },
    search() {
      if (!this.inputVal){
        uni.navigateBack({
            delta: 1
        });
        return;
      }
      // console.log(this.inputVal);
      this.$emit("search", this.inputVal);
			let searchHistory = this.searchHistory;
			//只存储10个历史
			if (searchHistory.length >= 10) {
				searchHistory.pop ();
				searchHistory.unshift(this.inputVal);
			} else {
				searchHistory.unshift(this.inputVal);
			}
      this.setHistory(searchHistory)
      uni.navigateTo({
        url: `/pages/product/goods-list?key=${this.inputVal}`
      });
    }
  },
  watch: {
    inputVal(newVal) {
      if (newVal) {
        this.searchName = "搜索";
        this.isDelShow = true;
      } else {
        this.searchName = "取消";
        this.isDelShow = false;
      }
    }
  }
};
